import BigNumber from 'bignumber.js'
import erc20 from 'config/abi/erc20.json'
import masterchefABI from 'config/abi/masterchef.json'
import multicall from 'utils/multicall'
import { getAddress, getMasterChefAddress } from 'utils/addressHelpers'

import getFarmMasterChefAbi from 'utils/getFarmMasterChefAbi'
import getFarmMasterChefAddress from 'utils/getFarmMasterChefAddress'
import farmsConfig from 'config/constants/farms'
import { BIG_TEN, BIG_TWO } from '../../utils/bigNumbers'
import { QuoteToken } from '../../config/constants/types'

export const fetchMasterChefGlobalData = async () => {
  const [ tokenPerBlock ] = await multicall( masterchefABI, [
    {
      address: getMasterChefAddress(),
      name: 'tokenPerBlock',
    },
  ] )

  return { tokenPerBlock: new BigNumber( tokenPerBlock ).div( BIG_TEN.pow( 18 ) ).toNumber() }
}

export const fetchFarms = async () => {
  return Promise.all(
    farmsConfig.map( async ( farmConfig ) => {
      const lpAddress = getAddress( farmConfig.lpAddresses )
      const farmMasterChefAddress = getMasterChefAddress()

      const calls = [
        // Balance of token in the LP contract
        {
          address: getAddress( farmConfig.tokenAddresses ),
          name: 'balanceOf',
          params: [ lpAddress ],
        },
        // Balance of quote token on LP contract
        {
          address: getAddress( farmConfig.quoteTokenAddresses ),
          name: 'balanceOf',
          params: [ lpAddress ],
        },
        // Balance of LP tokens in the master chef contract
        {
          address: farmConfig.isStakePool ? getAddress( farmConfig.tokenAddresses ) : lpAddress,
          name: 'balanceOf',
          params: [ farmMasterChefAddress ],
        },
        // Total supply of LP tokens
        {
          address: lpAddress,
          name: 'totalSupply',
        },
        // Token decimals
        {
          address: getAddress( farmConfig.tokenAddresses ),
          name: 'decimals',
        },
        // Quote token decimals
        {
          address: getAddress( farmConfig.quoteTokenAddresses ),
          name: 'decimals',
        },
      ]

      let tokenBalanceLP = null
      let quoteTokenBalanceLP = null
      let lpTokenBalanceMC = null
      let lpTotalSupply = null
      let tokenDecimals = null
      let quoteTokenDecimals = null

      try {
        [
          tokenBalanceLP,
          quoteTokenBalanceLP,
          lpTokenBalanceMC,
          lpTotalSupply,
          tokenDecimals,
          quoteTokenDecimals,
        ] = await multicall( erc20, calls )
      } catch ( e ) {
        // console.group( 'ERROR FETCHING FARM DATA' )
        // console.log( 'farmConfig:', farmConfig )
        // console.log( 'calls:', calls )
        // console.log( '%c error', 'background: red; color: yellow', e.toString() )
        // console.groupEnd()
      }

      if ( lpTotalSupply <= 0 ) {
        console.warn( ' No liquidity for', farmConfig.lpSymbol )
      }

      let tokenAmountTotal = new BigNumber( tokenBalanceLP ).div( BIG_TEN.pow( tokenDecimals ) )
      let tokenPriceVsQuote = new BigNumber( quoteTokenBalanceLP ).div( BIG_TEN.pow( quoteTokenDecimals ) )
      let lpTotalInQuoteToken = new BigNumber( quoteTokenBalanceLP ).div( BIG_TEN.pow( quoteTokenDecimals ) )
      let lpTokenRatio = null

      if ( farmConfig.isStakePool ) {
        // ** STAKE POOLS ** //

        lpTokenRatio = new BigNumber( lpTokenBalanceMC ).div( new BigNumber( tokenBalanceLP ) )

        lpTotalInQuoteToken = lpTotalInQuoteToken.times( lpTokenRatio )
      } else {
        // ** LP-PAIRS ** //

        // Ratio in % a LP tokens that are in staking, vs the total number in circulation
        lpTokenRatio = new BigNumber( lpTokenBalanceMC ).div( new BigNumber( lpTotalSupply ) )

        // Total value in staking in quote token value
        lpTotalInQuoteToken = lpTotalInQuoteToken.times( lpTokenRatio ).times( BIG_TWO )

        // Amount of token in the LP that are considered staking (i.e amount of token * lp ratio)
        tokenAmountTotal = tokenAmountTotal.times( lpTokenRatio )
      }

      if ( farmConfig.tokenSymbol === QuoteToken.USDC && farmConfig.quoteTokenSymbol === QuoteToken.USDC ) {
        tokenPriceVsQuote = new BigNumber( 1 )
      } else {
        tokenPriceVsQuote = tokenPriceVsQuote.times( lpTokenRatio )
      }

      // if ( farmConfig.pid === 2 ) {
      //   console.group( farmConfig.lpSymbol )
      //
      //   console.log( { calls } )
      //
      //   console.log( {
      //     farmPid: farmConfig.pid,
      //     farmSymbol: farmConfig.lpSymbol,
      //     tokenBalanceLP: new BigNumber( tokenBalanceLP ).toString(),
      //     quoteTokenBalanceLP: new BigNumber( quoteTokenBalanceLP ).toString(),
      //     lpTokenBalanceMC: new BigNumber( lpTokenBalanceMC ).toString(),
      //     lpTotalSupply: new BigNumber( lpTotalSupply ).toString(),
      //     lpTokenRatio: new BigNumber( lpTokenRatio ).toString(),
      //     tokenDecimals,
      //     quoteTokenDecimals,
      //     tokenAmountTotal: tokenAmountTotal.toString(),
      //     tokenPriceVsQuote: tokenPriceVsQuote.toString(),
      //     lpTotalInQuoteToken: lpTotalInQuoteToken.toString(),
      //   } )
      //
      //   console.groupEnd()
      // }

      const [ info, totalAllocPoint ] = await multicall( getFarmMasterChefAbi( farmConfig.type ), [
        {
          address: getFarmMasterChefAddress( farmConfig.type ),
          name: 'poolInfo',
          params: [ farmConfig.pid ],
        },
        {
          address: getFarmMasterChefAddress( farmConfig.type ),
          name: 'totalAllocPoint',
        },
      ] )

      const allocPoint = new BigNumber( info.allocPoint._hex )
      const poolWeight = allocPoint.div( new BigNumber( totalAllocPoint ) )

      return {
        ...farmConfig,
        tokenAmount: tokenAmountTotal.toJSON(),
        quoteTokenAmount: tokenPriceVsQuote.toJSON(),
        lpTotalInQuoteToken: lpTotalInQuoteToken.toJSON(),
        tokenPriceVsQuote: tokenPriceVsQuote.div( tokenAmountTotal ).toJSON(),
        poolWeight: poolWeight.toJSON(),
        multiplier: `${ allocPoint.div( 100 ).toString() }X`,
      }
    } ),
  )
}
