import React from 'react'
import styled from 'styled-components'
import { Text}  from 'uikit'
import { LogoIcon } from '../../../components/Svg'
import Skeleton from '../../../components/Skeleton/Skeleton'

interface Props {
  cakePriceUsd?: number;
  cakePriceMovr?: number;
  cakeTokenAddress?: string;
}

const Container = styled.div``

const PriceLink = styled.a`
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
  &:last-child{
    margin-bottom: 0;
  }
  svg {
    transition: transform 0.3s;
  }
  :hover {
    svg {
      transform: scale(1.2);
    }
  }
`

const TokenLabel = styled.span`
  color: ${({ theme }) => theme.colors.textDisabled};
  margin-left: 2px;
`

const CakePrice: React.FC<Props> = ( { cakePriceUsd, cakePriceMovr, cakeTokenAddress } ) => {
  const priceLink = `https://pancakeswap.info/token/${ cakeTokenAddress }`

  return (
    <Container>
      { cakePriceUsd ? (
        <PriceLink href={ priceLink } target="_blank">
          <LogoIcon width="24px" mr="8px"/>
          <Text color="textSubtle" bold>{ `$${ cakePriceUsd.toFixed( 2 ) }` }</Text>
          <TokenLabel>USDC</TokenLabel>
        </PriceLink>
      ) : (
        <Skeleton width={ 80 } height={ 24 }/>
      ) }

      { cakePriceMovr ? (
        <PriceLink href={ priceLink } target="_blank">
          <LogoIcon width="24px" mr="8px"/>
          <Text color="textSubtle" bold>{ `${ cakePriceMovr.toFixed( 2 ) }` }</Text>
          <TokenLabel>MOVR</TokenLabel>
        </PriceLink>
      ) : (
        <Skeleton width={ 80 } height={ 24 }/>
      ) }
    </Container>
  )
}

export default React.memo( CakePrice )
