// Pools
export const usePools = (account): Pool[] => {
  const { fastRefresh } = useRefresh()
  const dispatch = useDispatch()
  useEffect(() => {
    if (account) {
      dispatch(fetchPoolsUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  return useSelector( ( state: State ) => state.pools.data )
}

export const usePoolFromPid = (sousId): Pool => {
  return useSelector( ( state: State ) => state.pools.data.find( ( p ) => p.sousId === sousId ) )
}

// Prices
export const usePriceMovrUsdt = (): BigNumber => {
  const lpSymbol = 'MOVR-USDC LP' // MOVR-USDC LP
  const lp = useLPFromSymbol(lpSymbol)

  if(!lp){
    console.warn('usePriceMovrUsdt error:', lpSymbol, 'not found');
    return ZERO;
  }

  return lp.tokenPriceVsQuote ? new BigNumber(lp.tokenPriceVsQuote) : ZERO;
}

export const usePriceTokenUsdt = (): BigNumber => {
  const lpSymbol = 'TOKEN-USDC LP'
  const farm = useFarmFromSymbol(lpSymbol)

  if(!farm){
    console.warn('usePriceTokenUsdt error:', lpSymbol, 'not found');
    return ZERO;
  }

  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO;
}

export const usePriceTokenMatic = (): BigNumber => {
  const lpSymbol = 'TOKEN-DAI LP'
  const farm = useFarmFromSymbol(lpSymbol)

  if(!farm){
    console.warn('usePriceBossMovr error:', lpSymbol, 'not found');
    return ZERO;
  }

  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO;
}

export const usePriceEthUsdt = (): BigNumber => {
  const lpSymbol = 'ETH-USDC LP' // ETH-USDC LP
  const farm = useFarmFromSymbol(lpSymbol)
  const movrPriceUSD = usePriceMovrUsdt()

  if(!farm){
    console.warn('usePriceEthUsdt error:', lpSymbol, 'not found');
    return ZERO;
  }

  return farm.tokenPriceVsQuote ? movrPriceUSD.times(farm.tokenPriceVsQuote) : ZERO
}

