import React, { useContext } from 'react'
import { Menu as UikitMenu, useModal } from 'uikit'
import { useWeb3React } from '@web3-react/core'
import { allLanguages } from 'config/localisation/languageCodes'
import { LanguageContext } from 'contexts/Localisation/languageContext'
import useTheme from 'hooks/useTheme'
import useAuth from 'hooks/useAuth'
import { usePriceTokenUsdt, useEmperor, usePriceTokenMatic } from 'state/hooks'
import WalletConnectGuideModal from 'components/Modal/WalletConnectGuideModal'
import SettingModal from 'components/Modal/SettingModal'
import { getAddress } from '../../utils/addressHelpers'
import contracts from '../../config/constants/contracts'

import { config, socials } from './config'

const Menu = (props) => {
  const { account } = useWeb3React()
  const { login, logout } = useAuth()
  const { selectedLanguage, setSelectedLanguage } = useContext(LanguageContext)
  const { isDark, toggleTheme } = useTheme()

  const bossPriceUsd = usePriceTokenUsdt()
  const bossPriceMovr = usePriceTokenMatic()
  const bossTokenAddress = getAddress( contracts.token )

  const { myEmperor } = useEmperor()
  const myNickname = myEmperor.nickname

  const [onToggleSettingModal] = useModal(
    <SettingModal />,
  )

  const links = [...config]

  return (
    <>
      <WalletConnectGuideModal />
      <UikitMenu
        account={account}
        nickname={myNickname || ''}
        login={login}
        logout={logout}
        isDark={isDark}
        toggleTheme={toggleTheme}
        currentLang={selectedLanguage && selectedLanguage.code}
        langs={allLanguages}
        setLang={setSelectedLanguage}
        cakePriceUsd={bossPriceUsd.toNumber()}
        cakePriceMovr={bossPriceMovr.toNumber()}
        cakeTokenAddress={bossTokenAddress}
        links={links}
        socials={socials}
        onSettingClick={onToggleSettingModal}
        // profile={{
        //   username: profile?.username,
        //   image: profile?.nft ? `/images/nfts/${profile.nft?.images.sm}` : undefined,
        //   profileLink: '/profile',
        //   noProfileLink: '/profile',
        //   showPip: !profile?.username,
        // }}
        {...props}
      />
    </>
  )
}

export default Menu
